.root {
  background: #ffffff;
  box-shadow: 0px 12px 24px rgba(87, 111, 172, 0.08);
  border-radius: 12px;
  display: flex;
  flex: 1;
  height: 100vh;
  min-width: 1px;
  min-height: 1px;
  max-height: calc(100vh - 112px);
}

.left {
  flex: 0 0 37.67%;
  max-width: 37.67%;
  border-right: 1px solid #dde0eb;
  display: flex;
  flex-direction: column;
}
.tools {
  padding: 16px 20px;
  display: flex;
  border-bottom: 1px solid #dde0eb;
  flex-shrink: 0;
}

.tabs {
  display: flex;
}
.tab {
  margin-right: 20px;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  color: #a2a7b8;
  position: relative;
  display: block;
  transition: 0.3s;

  &:last-child {
    margin-right: 0;
  }

  &:after {
    content: '';
    display: block;
    background: #376fff;
    border-radius: 4px 4px 0px 0px;
    position: absolute;
    height: 3px;
    left: 0;
    right: 0;
    top: calc(100% + 14px);
    opacity: 0;
    visibility: hidden;
    transition: 0.3s;
  }
}
.tabActive {
  color: #376fff;

  &:after {
    opacity: 1;
    visibility: visible;
  }
}
.new {
  margin-left: auto;
}

.list {
  flex: 1;
  min-width: 1px;
  min-height: 1px;
  overflow: auto;
  padding-bottom: 20px;
}

.item {
  padding: 24px;
  border-bottom: 1px solid #dde0eb;
  cursor: pointer;

  &:hover {
    .title {
      color: #fab62f;
    }
  }
}

.itemActive {
  background-color: #f5f6fa;
}

.itemTop {
  display: flex;
  margin-bottom: 16px;
  align-items: center;
}
.date {
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  color: #b4bcd6;
  padding-right: 30px;
}

.resolved {
  margin-left: auto;
  flex-shrink: 0;
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  color: #b4bcd6;
  position: relative;
  z-index: 1;
  text-decoration: underline;
  transition: 0.3s;

  &:hover {
    color: #00d6b4;
  }
}
.title {
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #1e0e33;
  margin-bottom: 4px;
  transition: 0.3s;
}
.author {
  font-size: 14px;
  line-height: 17px;
  margin-bottom: 16px;
}
.type {
  font-weight: 600;
}
.name {
}
.message {
  color: #a2a7b8;
  font-size: 14px;
  line-height: 17px;
  white-space: nowrap;
  text-overflow: ellipsis;
  min-width: 1px;
  overflow: hidden;
}

.right {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-width: 1px;
}
